html, body {
  margin: 0;
  padding: 0;
}

.Features {
  background-image: url("../../background-image-2.png");
  height: 100%;
  width: 100%;
  padding: 5vh 0vh;
  background-size: cover;
  background-position: center;
}

.Features .container {
  width: 90%;
  margin-left: 5%;
}

.Features .container h2 {
  color: white;
  text-align: center;
}

.Features .container .row {
  width: 100%;
}

.Features .container .row .col-3 {
  width: 100%;
}

.Features .container .row .col-3 .feature-container {
  width: 100%;
  height: 100%;
  margin-top: 4vh;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Features .container .row .col-3 .feature-container .top {
  position: relative;
  background: rgba(198, 221, 255, 0.1);
  height: 100%;
  min-height: 245px;
  width: 100%;
  border-radius: 10px 10px 0px 0px;
}

.Features .container .row .col-3 .feature-container img {
  width: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Features .container .row .col-3 .feature-container .bottom {
  margin: 0;
  padding: 0;
  background: #334155;
  min-height: 100px;
  border-radius: 0px 0px 10px 10px;
  position: relative;
}

.Features .container .row .col-3 .feature-container .bottom h3 {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
