.header{
    position: fixed;
    z-index:999;
    background:transparent;
    width:100%;
    height:60px;
    display:flex;
    flex-wrap: wrap;

    nav{
        display:flex;
        width:100%;

        ul{
            list-style: none;   
            display: flex;
            width:100%;
            flex-direction: row;
            flex-wrap: wrap;

            li{
                color:white;
                font-family:600;
                font-size:1.6rem;
            }
        }
    }
}


@media only screen and (max-width:486px){
    li{
        display:none;
    }

    .header{
        height:60px;
        background:#FB2E55;
        width:100%;
        z-index:1;
        position: fixed;
    }
    .bars{
        color:white;
        font-size:1.8rem;
        font-weight:800;
        position: absolute;
        top:15px;
        right:15px;
    }
}