html,body{
    margin:0;
    padding:0;
}

.Features{
    background-image:url('../../background-image-2.png');
    height:100%;
    width:100%;
    padding:5vh 0vh;
    background-size:cover;
    background-position: center;
    
    .container{
        width:90%;
        margin-left:5%;

        h2{
            color:white;
            text-align: center;
        }
        .row{
            width:100%;

            .col-3{
                width:100%;

                .feature-container{
                    width:100%;
                    height:100%;
                    margin-top:4vh;
                    box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                    
                    .top{
                        position: relative;
                        background: rgba(198, 221, 255,0.1);
                        height:100%;
                        min-height:245px;
                        width:100%;
                        border-radius:10px 10px 0px 0px;
                    }

                    img{
                        width:100px;
                        position: absolute;
                        top:50%;
                        left:50%;
                        transform: translate(-50%, -50%);
                    }

                
                    .bottom{
                        margin:0;
                        padding:0;
                        background:#334155;
                        min-height:100px;
                        border-radius:0px 0px 10px 10px;
                        position: relative;

                        h3{
                            color:white;
                            position: absolute;
                            top:50%;
                            left:50%;
                            transform: translate(-50%, -50%);
                        }
                    }
                }
            }
        }
    }
}